
import { ref, defineComponent } from 'vue';
import Modal from '../../components/General/Modal.vue';
import BrandModel from './BrandModel';
import BrandService from './BrandsService';

export default defineComponent({
  components: {
    Modal,
  },
  props: {
    isEditing: {
      type: Boolean,
      default: () => false,
    },
  },
  setup(_, context) {
    const brand = ref<BrandModel>({
      name: '',
    });

    const setToEdit = (brandData: BrandModel) => {
      brand.value._id = brandData._id;
      brand.value.name = brandData.name;
    };

    const resetForm = () => {
      brand.value._id = '';
      brand.value.name = '';
    };

    const onSubmit = async () => {
      try {
        if (brand.value._id) {
          await BrandService.updateBrand(brand.value);
        } else {
          await BrandService.newBrand(brand.value);
        }
        console.log(brand.value.name);
        context.emit('close');
        resetForm();
      } catch (error) {
        console.log(error, '|error');
      }
    };

    return {
      onSubmit,
      resetForm,
      setToEdit,
      brand,
    };
  },
});
